



















































































































































import {Component, Vue, Watch} from 'vue-property-decorator';
import {
    CreateFacetValueDocument,
    DeleteOneFacetValueDocument,
    GetFacetDocument,
    SetFacetOnValueDocument
} from '../../../../gql';

@Component({
    layout: 'console',
    apollo: {
        facet: {
            query: GetFacetDocument,
            variables() {
                return {
                    id: this.$route.params.id
                }
            },
            pollInterval: 3000
        }
    }
})
export default class Id extends Vue {
    private name = ""
    private code = ""
    private isPrivate = true
    private init = false
    private values: any[] = []
    private add = false
    private valuecode = ''
    private facet

    private loading = false

    @Watch('facet')
    onGetFacet() {
        if (this.facet !== undefined) {
            if (!this.init) {
                this.name = this.facet.name
                this.code = this.facet.code
                this.isPrivate = this.facet.isPrivate
                this.init = true
            }
            this.values = this.facet.values
        }
    }

    onCreateFacetValues() {
        const load: any = this.$Message.loading('Action in progress..');
        this.loading = true
        this.$apollo.mutate({
            mutation: CreateFacetValueDocument,
            variables: {
                code: this.valuecode
            }
        }).then(value => {
            this.$apollo.mutate({
                mutation: SetFacetOnValueDocument,
                variables: {
                    id: value.data!.createOneFacetValue!.id,
                    rel: this.$route.params.id
                }
            }).then(value1 => {
                load()
                this.loading = false
                this.add = false
                this.valuecode = ''
                this.$notification.success({
                    description: 'Facet Values creation Successful',
                    message: 'Facet Values added'
                })
            }).catch(error => {
                load()
                this.$notification.error({
                    description: error.message,
                    message: 'Failed'
                })
            })
        })
    }

    onDelete(id) {
        const load: any = this.$Message.loading('Action in progress..');
        this.$apollo.mutate({
            mutation: DeleteOneFacetValueDocument,
            variables: {
                id: id
            }
        }).then(value => {
            load()
        }).catch(error => {
            load()
        })
    }

}
